.cocktail__list{
    padding-bottom: 30px;
}

.cocktail {
    width: 100%;
    background-color: #181818;
    padding: 30px;
    background-image: url('./../assets/section-bg.png');
    background-repeat: no-repeat;
    background-position: center;
}

.cocktail__content{
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 30px;
    align-items: start;
}

.cocktail:not(:last-child){
    margin-bottom: 30px;
}

.cocktail__img{
    width: 240px;
    height: 240px;
}

.cocktail__img--small{
    width: 200px;
    height: 200px;
}

.cocktail__info{
    width: 100%;
}

.cocktail__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;  
    gap: 15px; 
}
.cocktail__top button {
    min-width: 161px;
}
.cocktail__title{
    display: inline-block;
    font-size: 40px;
}

.cocktail__categories{
    display: flex;
    margin-top: 25px;
}
.cocktail__categories h3:not(:last-child){
    padding-right: 15px;
    border-right: 1px solid #d16868;
}
.cocktail__categories h3:not(:first-child){
    padding-left: 15px;
}
.cocktail__ingredients{
    margin-top: 40px;
    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: start;
    flex-wrap: wrap;
}

.plus{
    font-size: 60px;
}

.cocktail__byingr{
    display: flex;
    gap: 20px;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
}
.byingr__btn{
    margin-left: auto;
}


@media(max-width: 1360px){
    .cocktail__list{
        padding-bottom: 20px;
    }
    .cocktail:not(:last-child){
        margin-bottom: 20px;
    }
}

@media(max-width: 1113px) {
    .cocktail {
        padding: 15px;
        gap: 20px;
    }
    .cocktail__title{
        font-size: 36px;
    }
    .cocktail__ingredients{
        margin-top: 30px;
    }
    .cocktail__ingredients img{
        width: 80px;
        height: 80px;
    }
    .plus{
        font-size: 30px;
    }
}

@media(max-width: 876px) {
    .cocktail__content {
        flex-direction: column-reverse;
    }
    .cocktail__categories{
        margin-top: 20px;
    }
    .cocktail__ingredients{
        /* gap: 5px; */
        margin-top: 20px;
    }
}

@media(max-width: 643px) {
    .byingr__btn{
        margin-left: auto;
    }
    .cocktail__img--small{
     margin: 0 auto;
    }
    .cocktail__byingr{
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .cocktail__bying h2{
        width: 100%;
        text-align: center;
    }
}

@media(max-width: 606px) {
    .cocktail__title{
        font-size: 32px;
    }
    .cocktail__ingredients{
        flex-direction: column;
    }

    .cocktail__img{
        margin: 0 auto;
    }   
}