.cocktail {
   height: auto;
   margin: 0 auto;
   padding-bottom: 30px;
}
.cocktail__wrapper{
    width: 100%;
    margin: 0 auto;
    background-color: #181818;
    padding: 30px;
    position: relative;
    background-image: url('./../assets/section-bg.png');
    background-repeat: no-repeat;
    background-position: center;
}
.cocktail__content{
    display: flex;
    gap: 30px;
    margin-bottom: 40px;
}

.cocktail__img{
    width: 280px;
    height: 280px;
}
.cocktail__content-info{
    width: 100%;
}
.cocktail__title{
    margin-bottom: 20px;
}
.cocktail__categories{
    display: flex;
    margin-bottom: 20px;
}
.cocktail__categories h3:not(:last-child){
    padding-right: 15px;
    border-right: 1px solid #d16868;
}
.cocktail__categories h3:not(:first-child){
    padding-left: 15px;
}
.cocktail__ingredients{
    margin-top: 40px;
    display: flex;
    gap: 20px;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;   
}
.plus{
    font-size: 60px;
}
.close__btn{
    position: absolute;
    top: 30px;
    right: 30px;
    width: 32px;
    height: 32px;
    background-color: transparent;
}
.close__btn span{
    display: block;
    width: 100%;
    height: 2px;
    background-color: #fff;
    position: absolute;
    transform: translateY(-50%);
}
.close__btn span:nth-child(1) {
    transform: rotate(45deg);
    top: 50%;
    left: 0;
}
.close__btn span:nth-child(2) {
    transform: rotate(-45deg);
    top: 50%;
    right: 0;
}
@media (max-width: 1040px) {
    .cocktail__img{
        width: 260px;
        height: 260px;
    }
}

@media(max-width: 1360px) {
    .cocktail {
        padding-bottom: 20px;
    }   
}

@media (max-width: 984px) {
    .cocktail__img{
        width: 240px;
        height: 240px;
    }
    .cocktail__title{
        font-size: 36px;
    }
}

@media (max-width: 700px) {
    .cocktail__wrapper{
        padding: 30px 15px;
    }
    .cocktail__content{
        flex-wrap: wrap;   
    }
    .cocktail__img{
        margin: 0 auto;
    }
    .cocktail__title{
       text-align: center;
    }
    .cocktail__categories{
        justify-content: center;
    }
    .cocktail__ingredients{
        margin-top: 20px;
        gap: 10px;
        justify-content: flex-start;
    }
    .plus{
        font-size: 30px;
    }
    .cocktail__title{
        font-size: 32px;
    }
}

@media(max-width: 404px) {
    .cocktail__wrapper{
        padding: 65px 15px 30px;
    }
    .cocktail__content{
        margin-bottom: 30px;   
    }
    .cocktail__ingredients{
        flex-direction: column;
    }
    .cocktail__categories{
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }
    .cocktail__categories h3:not(:last-child){
        padding-right: 0;
        border-right: none;
    }
    .close__btn{
        top: 20px;
        right: 20px;
    }
}