
/*Обнуление*/
*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;
color: inherit;
}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{
	font-size:inherit;
	font-family: "Noto Serif Display", serif;
 	font-weight: 700;
}
/*--------------------*/


body {
  min-height: 101vh;
  height: 100%;
  background-color: #2C2C2C;
  color: #ffffff;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}
#root{

  height: 100%;
}

.container{
  max-width: 1350px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  height: inherit;
}
.title{
	font-size: 44px;
	line-height: 1;
	letter-spacing: 0.02em;
}
.subtitle{
	font-size: 18px;
	line-height: 1;
	color: #d16868;
}
.text{
	font-weight: 400;
	font-size: 14px;
	line-height: 1.5;
	letter-spacing: -0.02em;
}



.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
	top: 40%;
  left: 0;
  right: 0;
  
  height: 100%;

  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: absolute;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

.error{
  text-align: center;
  color: red;
  margin-top: 30px;
  font-size: 18px;
  font-weight: 700px;

}

@media(max-width: 984px) {
  .subtitle{
    font-size: 16px;
  }
}