.button{
    background-color: #d16868;
    color: #ffffff;
    font-weight: 700;
    font-size: 16px;
    line-height: 1;
    text-align: center;
    font-family: "Noto Serif Display", serif;
    transition: all .3s ease;
    line-height: 1;
    padding: 10px 24px;
}
.button:hover{
    opacity: 0.9;
}
.button:active{
    opacity: 0.8;
}
@media(max-width: 440px) {
    .button{
        font-size: 14px;
    }
}