.search{
    margin-top: 20px;
}
.search__inner{
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    height: 36px;
    position: relative;
}
.search__inner input {
    padding: 10px 18px;
    width: 240px;
}
.search__inner input::placeholder {
    font-size: 16px;
    text-align: center;
    line-height: 1;
}
.search__message{
    position: absolute;
    bottom: -52px;
    left: 50%;
    transform: translateY(-50%);
    background-color: #ffffff;
    padding: 8px 15px;
    color: black;
    border-radius: 5px;
}

.search__message::before{
    content: "";
    width: 7px;
    height: 7px;
    border: 0 solid transparent;
    border-right-width: 7px;
    border-left-width: 7px;
    border-bottom: 10px solid #fff;
    top: -9px;
    left: 18px;
    position: absolute;
}

@media(max-width: 717px) {
    .search__inner input {
        width: 100%;
    }
}

@media(max-width: 440px) {
    .search{
        margin-top: 15px;
    }
    .search__inner{
        gap: 15px;
    }
    .search__inner input::placeholder {
        font-size: 14px;
    }
    .search__inner input {
        padding: 9px 18px;
    }
}