.ingredient{
    display: flex;
    flex-direction: column;
    width: 140px;
    align-items: center;
}
.ingredient__img{
    width: 140px;
    height: 140px;
    display: block;
    margin-bottom: 20px;
}
.ingredient h3{
    text-align: center;    
    margin-bottom: 15px;
}
.measure{
    text-align: center;
}
@media(max-width: 1360px) {
    .ingredient__img{
        width: 120px;
        height: 120px;
        margin-bottom: 16px;
    }
}
@media(max-width: 600px) {
    .ingredient__img{
        width: 80px;
        height: 80px;
    }
    .ingredient h3{ 
        margin-bottom: 10px;
    }
}